#treeelement {
  margin: 0;
  padding: 0;
  display: flex;
  flex: 1 1 auto;
  height: 100%;
  width: 100%;
  height: 100vh;
  width: 100%;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
    'Helvetica Neue', sans-serif;
}

.wrapper {
  height: 100%;
  width: 100%;
  margin: 0;
}

.wrapper {
  display: flex;
  flex-direction: column;
}

.bar-wrapper {
  margin-top: 5px;
  margin-left: 5px;
}

.tree-wrapper {
  height: calc(100% - 25px);
  overflow-y: scroll;
}

.previous {
  margin-left: 10px;
}

.next,
.collapse {
  margin-right: 10px;
  margin-left: 5px;
}
